.page-header {
  margin: 0;
  padding: 28px 0 48px 0;

  background-color: $deep-red;
  background-image: url("../img/background_image_desktop_1x.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 0 0;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/background_image_desktop_2x.jpg");
  }

  @media (max-width: $desktop-width) {
    padding: 38px 0 82px 0;
  }

  @media (max-width: $tablet-width) {
    padding: 18px 0 55px 0;
  }
}

.page-header__group {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;

  span,
  a {
    font-family: $font--bebasneue-regular;
    font-style: normal;
    font-weight: normal;
    font-size: 26px;
    line-height: 26px;
    letter-spacing: 0.03em;

    color: $white;
  }

  @media (max-width: $desktop-width) {
    &:first-child {
      margin: 0 0 20px 0;
    }

    span,
    a {
      font-size: 24px;
      line-height: 24px;
    }
  }

  @media (max-width: $tablet-width) {
    &:first-child {
      margin: 0 0 75px 0;
    }

    span,
    a {
      font-size: 20px;
      line-height: 20px;
    }
  }
}

.page-header__info {
  position: relative;
  top: -47px;
  left: 19px;

  display: flex;
  flex-direction: column;

  margin: 0 26px 0 0;

  width: 40%;
  max-width: 377px;

  span {
    font-size: 49px;
    line-height: 59px;
    text-transform: uppercase;
    text-align: right;
  }

  h1 {
    font-style: normal;
    font-weight: bold;
    font-size: 109px;
    line-height: 109px;
    letter-spacing: 0.03em;
    text-align: right;

    color: $white;
  }

  @media (max-width: $desktop-width) {
    top: -3px;
    left: 0;

    width: 47.8%;

    span {
      font-size: 34px;
      line-height: 41px;
    }

    h1 {
      font-size: 86px;
      line-height: 86px;
    }
  }

  @media (max-width: $tablet-width) {
    align-self: center;

    margin: 0 auto;

    width: 100%;

    span {
      margin: 0 0 5px 0;

      font-size: 28px;
      line-height: 33px;
    }

    h1 {
      margin: 0 0 4px 0;

      font-size: 78px;
      line-height: 78px;
    }
  }
}

.page-header__list {
  position: relative;
  top: -82px;

  margin: 0;
  padding: 0;

  max-width: 115px;

  list-style: none;

  li {
    font-family: $font--roboto-regular;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 21px;
    text-align: right;

    color: $white;
  }

  @media (max-width: $desktop-width) {
    top: -75px;
  }

  @media (max-width: $tablet-width) {
    left: -2px;
  }
}

.page-header__button {
  align-self: flex-end;

  margin: 0;
  padding: 20px 53px;

  max-width: 62%;

  background-color: transparent;
  border: 4px solid $white;
  color: $white;

  @media (max-width: $desktop-width) {
    position: relative;
    top: -20px;

    padding: 20px 30px;
  }

  @media (max-width: $tablet-width) {
    width: 100%;
    max-width: 100%;
  }
}

.page-header__img {
  position: relative;
  top: -3px;

  width: 48%;
  max-width: 500px;
  height: 556px;

  background-image: url("../img/athletes/sportsman_desktop_1x.jpg");
  background-size: cover;
  background-repeat: no-repeat;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/athletes/sportsman_desktop_2x.jpg");
  }

  @media (max-width: $desktop-width) {
    top: 0;

    width: 324px;
    height: 390px;

    background-image: url("../img/athletes/sportsman_tablet_1x.jpg");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/athletes/sportsman_tablet_2x.jpg");
    }
  }

  @media (max-width: $tablet-width) {
    display: none;
  }
}

.page-header__social {
  position: relative;
  top: -45px;

  display: flex;
  flex-direction: column;
  align-items: flex-end;

  margin: 0;
  padding: 0;

  width: 12%;

  list-style: none;

  li {
    margin: 0 0 15px 0;

    &:last-child {
      margin: 0;
    }
  }

  a {
    font-family: $font--roboto-regular;
    font-size: 12px;
    line-height: 14px;
    text-transform: uppercase;
  }

  @media (max-width: $desktop-width) {
    display: none;
  }
}
